import React from 'react';
import * as ReactRouterDOM from "react-router-dom";
import { Container, Row, Col } from "react-bootstrap";

import MenuComponent from "./menu";
import GameTypeOneComponent from "./gametypeone";
import GameTypeTwoComponent from "./gametypetwo";

const Router = ReactRouterDOM.BrowserRouter;
const Route = ReactRouterDOM.Route;
const Switch = ReactRouterDOM.Switch;

export default class App extends React.Component
{
    public render()
    {
        return (
            <Container fluid>
                <Router basename={process.env.PUBLIC_URL}>
                    <Switch>
                        <Route exact path="/" component={ MenuComponent } />
                        <Route exact path="/typeone" component={ GameTypeOneComponent } />
                        <Route exact path="/typetwo" component={ GameTypeTwoComponent } />
                    </Switch>
                </Router>
            </Container>
        )
    }   
}
